import {
  SourceSelectionInitialStateFull,
  SourceSelectorSelectedPackageInfo,
  SourceSelectorSelectedSourceInfo,
} from "../sourceSelector.types"

type ResetSourceSelectorStateProps = {
  initialStateFull: SourceSelectionInitialStateFull | null
  setSelectedAllPackages: (packages: Array<SourceSelectorSelectedPackageInfo>) => void
  setSelectedSources: (sources: Array<SourceSelectorSelectedSourceInfo>) => void
  setIsFilterInclude: (isFilterInclude: boolean) => void
}

function resetSourceSelectorState({
  setSelectedAllPackages,
  setSelectedSources,
  setIsFilterInclude,
  initialStateFull,
}: ResetSourceSelectorStateProps): void {
  setSelectedAllPackages(initialStateFull?.packages || [])
  setSelectedSources(initialStateFull?.sources || [])
  setIsFilterInclude(initialStateFull?.isFilterInclude ?? true)
}

export { resetSourceSelectorState }
